.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  max-width: 100%;
  width: 24rem;
  height: 5.5rem;
  padding: 0.8rem 1.2rem;
  background-color: #3B8E65;
  font-family: "Noto Sans JP";
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 100%;
  letter-spacing: 0.2em;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  border-radius: 0.8rem;
  cursor: pointer;
  z-index: 0;
}

.btn.btn-green {
  background-color: #009770;
}

.btn.btn-green:hover {
  color: #009770;
}

.btn.btn-green::after {
  border: 2px solid #009770;
}

.btn.btn-orange {
  background-color: #FA774F;
}

.btn.btn-orange:hover {
  color: #FA774F;
}

.btn.btn-orange::after {
  border: 2px solid #FA774F;
}

.btn::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  border: 2px solid #3B8E65;
  z-index: 2;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.btn:hover {
  color: #3B8E65;
}

.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.btn span {
  position: relative;
  color: inherit;
  font-weight: inherit;
  text-decoration: none;
  z-index: 3;
}

.btn.btn-yellow {
  background: #ffc520;
  color: #FFF;
  border-radius: 5px;
}

.btn.btn-yellow::after {
  border: unset;
}

.btn.btn-yellow::before {
  background: #009770;
}

@media only screen and (max-width: 768px) {
  .btn {
    padding: 1.3rem 6.0rem 1.3rem 3.2rem;
    font-size: 1.6rem;
    line-height: 2.4rem;
    letter-spacing: 0.1em;
    border-radius: 2.5rem;
  }
  .btn::after {
    border-width: 2px;
  }
}

