.mobile-nav-toggle {
  position: fixed;
  right: 2.0rem;
  top: 62px;
  width: 28px;
  height: 22px;
  padding: 0;
  background: none;
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 999;
  margin: 0;
  transition: all 0.4s ease-in-out;
}

@media only screen and (max-width: 768px) {
  .mobile-nav-toggle {
    display: inline-block;
    top: 2.75rem;
  }
}

.mobile-nav-toggle .toggle-icon {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
}

.mobile-nav-toggle .toggle-icon span {
  display: inline-block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 10px;
  background: #494949bf;
  transition: all 0.4s ease-in-out;
  transform-origin: center center;
}

@media only screen and (max-width: 768px) {
  .mobile-nav-toggle .toggle-icon span {
    background: #3472A1;
  }
}

.mobile-nav-toggle .toggle-icon span:nth-child(1) {
  top: 0;
}

.mobile-nav-toggle .toggle-icon span:nth-child(2) {
  top: 10px;
}

.mobile-nav-toggle .toggle-icon span:nth-child(3) {
  bottom: 0;
}

.mobile-nav-toggle.toggle-active .toggle-icon span {
  background-color: #fff;
}

.mobile-nav-toggle.toggle-active .toggle-icon span:nth-child(1) {
  transform: translateY(10px) rotate(45deg);
}

.mobile-nav-toggle.toggle-active .toggle-icon span:nth-child(2) {
  opacity: 0;
  transform: translateY(10px) rotate(-45deg);
  visibility: hidden;
}

.mobile-nav-toggle.toggle-active .toggle-icon span:nth-child(3) {
  transform: translateY(-10px) rotate(-45deg);
}

#mobile-nav {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 998;
  right: -100%;
  width: 100%;
  overflow-y: auto;
  transition: all 0.4s ease-in-out;
  background: #3472A1;
}

#mobile-nav .mobile-toggle {
  position: absolute;
  top: 2.0rem;
  right: 1.8rem;
  display: none;
  width: 2.4rem;
  height: 1.7rem;
  padding: 0;
  background: none;
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 9;
}

@media only screen and (max-width: 768px) {
  #mobile-nav .mobile-toggle {
    display: block;
  }
}

#mobile-nav .mobile-toggle span {
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 1px;
  background: #FFFFFF;
  transition: all 0.4s ease-in-out;
  transform-origin: center center;
}

#mobile-nav .mobile-toggle span:nth-child(1) {
  top: 0;
}

#mobile-nav .mobile-toggle span:nth-child(2) {
  top: 0.8rem;
}

#mobile-nav .mobile-toggle span:nth-child(3) {
  bottom: 0;
}

#mobile-nav .mobile-toggle.toggle-active span:nth-child(1) {
  transform: translateY(0.8rem) rotate(-45deg);
}

#mobile-nav .mobile-toggle.toggle-active span:nth-child(2) {
  opacity: 0;
  transform: translateX(2rem);
  visibility: hidden;
}

#mobile-nav .mobile-toggle.toggle-active span:nth-child(3) {
  transform: translateY(-0.8rem) rotate(45deg);
}

